.card {
  width: 200px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  font-family: Roboto, sans-serif;
  font-size: 10px;
  padding: 32px 16px 16px;
}

.card__image {
  max-height: 134px;
}

.card__title {
  padding-top: 40px;

  line-height: 18px;
  font-size: 12px;
  font-weight: 500;
}

.card__serial-number {
  padding-top: 8px;
  padding-bottom: 16px;
  color: var(--light-grey-color);
}

.card__price {
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__price-text {
  font-size: 12px;
  color: var(--light-grey-color);
}

.card__price-amount {
  font-size: 16px;
  font-weight: 700;
  color: #060b35;
}

.card__link {
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  text-align: center;

  padding: 12px 0;
  background-color: var(--blue-color);
  border: 1px solid transparent;
  border-radius: 5px;
}

.card__link:hover {
  color: var(--blue-color);
  background-color: #fff;
  border-color: var(--blue-color);
}
